$primary: #333;
$color: white;

@font-face {
  font-family: DrukWide;
  src: url(../fonts/DrukWide-Medium.ttf);
}

* {
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
  color: $color;
  box-sizing: border-box;
  font-family: "Nunito", sans-serif;
  font-weight: 400;
}
body {
  background-color: $primary;
}
button {
  background: none;
  border: none;
  cursor: pointer;
}
.container {
  padding-inline: 10%;
  max-width: 90rem;
}
h1,
h2,
h3 {
  font-weight: 700;
}

header {
  background-color: rgba(68, 68, 68, 0.8);
  backdrop-filter: saturate(180%) blur(20px);
  padding-block: 2rem;
  box-shadow: rgba(0, 0, 0, 0.2) 0 0 2rem;
  position: fixed;
  width: 100%;
  z-index: 9999;
  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    img {
      height: 2rem;
      width: auto;
    }
    nav {
      display: flex;
      justify-content: space-around;
      align-items: center;
      ul {
        display: flex;
        align-items: center;
        gap: 2rem;
      }
    }
    button {
      border: white 2px solid;
      padding: 0.5rem 1rem;
      border-radius: 9999rem;
      font-size: 1rem;
    }
  }
}
main {
  padding-top: 2rem;
  height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  gap: 10%;
  position: relative;
  .background-img {
    position: absolute;
    top: 0;
    left: 0;
    z-index: -9999;
    height: 100%;
    width: 100%;
    filter: blur(0.5rem) opacity(50%);
    background-image: url("https://images.unsplash.com/photo-1555881400-74d7acaacd8b?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=2670&q=80");
    background-size: cover;
    background-position: center;
  }
  .left {
    flex: 1;
    h1 {
      text-transform: uppercase;
      font-weight: 700;
      font-size: 4vw;
      text-align: justify;
      margin-bottom: 1rem;
      font-family: "DrukWide";
      line-height: 120%;
    }
    p {
      width: 100%;
      opacity: 0.5;
    }
  }
  .right {
    flex: 1;
    padding: 2rem;
    background-color: rgba(68, 68, 68, 0.8);
    border-radius: 1rem;
    backdrop-filter: saturate(180%) blur(20px);
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: center;
    justify-content: center;
    img {
      border-radius: 0.5rem;
      width: 100%;
      height: auto;
      object-fit: cover;
      object-position: center;
    }
    span {
      opacity: 0.5;
      text-align: center;
    }
  }
}
.works {
  background-color: white;
  padding-block: 2rem;
  * {
    color: $primary;
  }
  h2 {
    font-size: 3rem;
    display: flex;
    align-items: center;
    gap: 1rem;
    i {
      font-size: 2rem;
    }
  }
  .grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(17rem, 1fr));
    gap: 2rem;
    width: 100%;
    padding-top: 1rem;
    .grid-item {
      background-color: $primary;
      padding: 2rem;
      border-radius: 1rem;
      * {
        color: white;
      }
      p {
        padding-top: 1rem;
        opacity: 0.5;
      }
      a {
        width: fit-content;
        margin-top: 1rem;
        padding: 0.5rem 1rem;
        border: 2px white solid;
        border-radius: 9999;
        display: flex;
        align-items: center;
        gap: 0.5rem;
        font-size: 0.7rem;
        i {
          font-size: 1rem;
        }
      }
    }
  }
}
.contact {
  background-color: white;
  padding-block: 2rem;
  * {
    color: $primary;
  }
  h2 {
    font-size: 3rem;
    display: flex;
    align-items: center;
    gap: 1rem;
    i {
      font-size: 2rem;
    }
  }
  .contact-block {
    width: 100%;
    height: 70vh;
    display: flex;
    overflow: hidden;
    border-radius: 1rem;
    margin-top: 1rem;
    .left {
      width: 50%;
      background-image: url(https://images.unsplash.com/photo-1488590528505-98d2b5aba04b?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1740&q=80);
      background-size: cover;
      background-position: center;
    }
    .right {
      width: 50%;
      display: flex;
      gap: 0.5rem;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      background-color: $primary;
      * {
        color: white;
      }
      h3 {
        font-size: 2rem;
        margin-bottom: 1rem;
      }
      div {
        display: flex;
        flex-direction: column;
        width: fit-content;
        span {
          display: flex;
          gap: 1rem;
          align-items: center;
          line-height: 180%;
        }
        .contact-mail {
          margin-top: 1rem;
          width: fit-content;
          margin-top: 1rem;
          padding: 0.5rem 1rem;
          border: 2px white solid;
          border-radius: 9999;
          display: flex;
          align-items: center;
          gap: 0.5rem;
          font-size: 1rem;
          i {
            font-size: 1rem;
          }
        }
      }
    }
  }
}
footer {
  padding-block: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  #copyright {
    text-align: center;
    opacity: 0.5;
    font-size: 0.7rem;
  }
}

@media screen and (max-width: 1000px) {
  .container {
    padding-inline: 5%;
  }
  header {
    padding-block: 1.5rem;
    nav {
      display: none;
      ul {
        gap: 1rem !important;
      }
    }
    button {
      display: none;
    }
  }
  main {
    .left {
      h1 {
        font-size: 7vw;
      }
    }
    .right {
      display: none;
    }
  }
  .contact {
    .left {
      display: none;
    }
    .right {
      width: 100% !important;
    }
  }
}
